@tailwind base;
@tailwind components;
@tailwind utilities;

@import url('https://fonts.googleapis.com/css2?Merriweather:wght@200,300;400;900&family=Raleway&display=swap');

@import url('https://fonts.googleapis.com/icon?family=Material+Icons+Outlined');

@layer base {

  h1,
  h2,
  h3,
  h4,
  h5,
  h6 {
    @apply font-serif;
    @apply font-light;
  }
}